import clsx from 'clsx';
import { useCallback } from 'react';

import { PuiIcon, PuiSvgIcon, PuiTypography } from '../..';
import { Divider, PopupMenu, PopupMenuItem, StyledPlaybackSpeedBox } from './StyledComponents';

interface PlaybackSpeedMenuProps {
  anchorEl: null | HTMLElement;
  handleClose: () => void;
  handleSelectSpeed: (speed: number) => void;
  activeSpeed: number;
  isFullscreen: boolean;
}

const SPEED_OPTIONS = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 2];

export const PlaybackSpeedMenu: React.FC<PlaybackSpeedMenuProps> = ({
  anchorEl,
  handleClose,
  handleSelectSpeed,
  activeSpeed,
  isFullscreen,
}) => {
  const handleItemClick = useCallback(
    (option: number) => {
      handleSelectSpeed(option);
      handleClose();
    },
    [handleSelectSpeed, handleClose]
  );

  return (
    <PopupMenu
      elevation={0}
      open={!!anchorEl}
      anchorEl={anchorEl}
      onClose={handleClose}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      transformOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      sx={{ transform: `translateY(-7px)` }}
      disablePortal={isFullscreen}
    >
      <StyledPlaybackSpeedBox>
        <PuiTypography
          variant='body-sm-regular'
          color='grey.300'
        >
          Playback speed
        </PuiTypography>
      </StyledPlaybackSpeedBox>
      <Divider />
      {SPEED_OPTIONS.map((option, index) => (
        <PopupMenuItem
          key={`${option}-${index}`}
          className={clsx({ active: option === activeSpeed })}
          onClick={() => handleItemClick(option)}
        >
          <PuiTypography
            variant='body-sm-regular'
            color='grey.500'
          >
            {option}x
          </PuiTypography>
          {option === activeSpeed && <PuiSvgIcon icon={PuiIcon.Check} />}
        </PopupMenuItem>
      ))}
    </PopupMenu>
  );
};
