import clsx from 'clsx';
import { useState } from 'react';

import { PuiBox, PuiIcon, PuiStack, PuiSvgIcon, PuiTypography } from '../../..';
import { ButtonTooltip } from './ButtonTooltip';
import { PlaybackSpeedMenu } from './PlaybackSpeedMenu';
import { PlayControls } from './PlayControls';
import {
  StyledButtonContainer,
  StyledControlsExpanded,
  StyledControlsMinimized,
  StyledControlsWrapper,
  StyledSlider,
  StyledSpeedContainer,
  StyledVolumeSlider,
} from './StyledComponents';
import type { UpdateVideoState, VideoPlayerState } from './types';

interface ControlsBottomProps {
  isFullscreen: boolean;
  showControls: boolean;
  videoState: VideoPlayerState;
  videoRef: React.RefObject<HTMLVideoElement>;
  updateVideoState: UpdateVideoState;
  togglePlay: () => void;
  toggleMute: () => void;
}

export const ControlsBottom = ({
  isFullscreen,
  showControls,
  videoState: { duration, currentTime, isPlaying, isMuted, volume, playbackRate },
  videoRef,
  updateVideoState,
  togglePlay,
  toggleMute,
}: ControlsBottomProps) => {
  const [playbackEl, setPlaybackEl] = useState<null | HTMLElement>(null);
  const [showVolumeSlider, setShowVolumeSlider] = useState(false);
  const progress = duration ? (currentTime / duration) * 100 : 0;

  const handlePlaybackClick = (event: React.MouseEvent<HTMLElement>) => {
    setPlaybackEl(event.currentTarget);
  };

  const onVolumeChange = (_: Event, value: number | number[]) => {
    const volume = Array.isArray(value) ? value[0] : value;
    const video = videoRef.current;

    if (!video) {
      return;
    }

    updateVideoState('volume', volume);
    video.volume = volume;
    const isMuted = volume === 0;
    updateVideoState('isMuted', isMuted);
    video.muted = isMuted;
  };

  const handleProgressChange = (_: Event | React.SyntheticEvent<Element, Event>, value: number | number[]) => {
    const val = Array.isArray(value) ? value[0] : value;
    updateVideoState('currentTime', (val / 100) * duration);
  };

  const handleProgressCommit = (_: Event | React.SyntheticEvent<Element, Event>, value: number | number[]) => {
    const val = Array.isArray(value) ? value[0] : value;
    const video = videoRef.current;
    if (video && duration) {
      video.currentTime = (val / 100) * duration;
    }
  };

  const handleClosePlaybackPopup = () => {
    setPlaybackEl(null);
  };

  const handleSelectSpeed = (speed: number) => {
    const video = videoRef.current;
    if (!video) {
      return;
    }

    updateVideoState('playbackRate', speed);
    video.playbackRate = speed;
  };

  return showControls ? (
    <StyledControlsExpanded className={clsx({ fullscreen: isFullscreen })}>
      <StyledSlider
        value={progress}
        min={0}
        max={100}
        step={0.1}
        onChange={handleProgressChange}
        onChangeCommitted={handleProgressCommit}
        className={clsx({ fullscreen: isFullscreen })}
      />
      <PuiStack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between' }}>
        <StyledControlsWrapper className={clsx({ fullscreen: isFullscreen })}>
          <PlayControls
            handleClick={togglePlay}
            currentTime={currentTime}
            duration={duration}
            isPlaying={isPlaying}
            isFullscreen={isFullscreen}
          />
          <PuiBox
            onMouseEnter={() => setShowVolumeSlider(true)}
            onMouseLeave={() => setShowVolumeSlider(false)}
            sx={{ display: 'flex', alignItems: 'center', gap: '8px' }}
          >
            <StyledButtonContainer
              className={clsx({ fullscreen: isFullscreen })}
              onClick={toggleMute}
            >
              <PuiSvgIcon
                width={isFullscreen ? 24 : 20}
                height={isFullscreen ? 24 : 20}
                icon={isMuted ? PuiIcon.VolumeX : PuiIcon.VolumeMax}
                stroke='white'
              />
            </StyledButtonContainer>
            {showVolumeSlider && (
              <StyledVolumeSlider
                value={volume}
                min={0}
                max={1}
                step={0.01}
                onChange={onVolumeChange}
              />
            )}
          </PuiBox>
        </StyledControlsWrapper>

        <ButtonTooltip title='Playback speed'>
          <StyledSpeedContainer onClick={handlePlaybackClick}>
            <PuiTypography
              variant='body-m-medium'
              color='white'
            >
              {playbackRate}x
            </PuiTypography>
          </StyledSpeedContainer>
        </ButtonTooltip>

        <PlaybackSpeedMenu
          anchorEl={playbackEl}
          handleClose={handleClosePlaybackPopup}
          activeSpeed={playbackRate}
          handleSelectSpeed={handleSelectSpeed}
          isFullscreen={isFullscreen}
        />
      </PuiStack>
    </StyledControlsExpanded>
  ) : (
    <StyledControlsMinimized>
      <PlayControls
        handleClick={togglePlay}
        currentTime={currentTime}
        duration={duration}
        isPlaying={isPlaying}
        isFullscreen={isFullscreen}
      />
    </StyledControlsMinimized>
  );
};
