import type { RefObject } from 'react';
import { useCallback, useEffect, useState } from 'react';

type FullscreenDocument = Document & {
  webkitFullscreenElement?: Element;
  mozFullScreenElement?: Element;
  msFullscreenElement?: Element;

  webkitExitFullscreen?: () => Promise<void> | void;
  mozCancelFullScreen?: () => Promise<void> | void;
  msExitFullscreen?: () => Promise<void> | void;
};

type FullscreenElement = HTMLElement & {
  webkitRequestFullscreen?: () => Promise<void> | void;
  mozRequestFullScreen?: () => Promise<void> | void;
  msRequestFullscreen?: () => Promise<void> | void;
};

export const useFullscreen = <T extends HTMLElement>(ref: RefObject<T>) => {
  const [isFullscreen, setIsFullscreen] = useState(false);
  const doc = document as FullscreenDocument;

  const getFullscreenElement = useCallback((): Element | null => {
    return (
      doc.fullscreenElement ||
      doc.webkitFullscreenElement ||
      doc.mozFullScreenElement ||
      doc.msFullscreenElement ||
      null
    );
  }, [doc]);

  const enterFullscreen = useCallback(() => {
    const el = ref.current as FullscreenElement | null;
    if (!el) return;

    (el.requestFullscreen || el.webkitRequestFullscreen || el.mozRequestFullScreen || el.msRequestFullscreen)?.call(el);
  }, [ref]);

  const exitFullscreen = useCallback(() => {
    (doc.exitFullscreen || doc.webkitExitFullscreen || doc.mozCancelFullScreen || doc.msExitFullscreen)?.call(doc);
  }, [doc]);

  const toggleFullscreen = useCallback(() => {
    const current = getFullscreenElement();
    if (current) {
      exitFullscreen();
    } else {
      enterFullscreen();
    }
  }, [getFullscreenElement, enterFullscreen, exitFullscreen]);

  useEffect(() => {
    const handleChange = () => setIsFullscreen(!!getFullscreenElement());
    const events = ['fullscreenchange', 'webkitfullscreenchange', 'mozfullscreenchange', 'MSFullscreenChange'] as const;

    events.forEach(e => document.addEventListener(e, handleChange));

    return () => events.forEach(e => document.removeEventListener(e, handleChange));
  }, [getFullscreenElement]);

  return { isFullscreen, enterFullscreen, exitFullscreen, toggleFullscreen };
};
