import type { FC, ReactElement } from 'react';

import { PuiTooltip, useTheme } from '../../..';

interface ButtonTooltipProps {
  title: string;
  children: ReactElement;
}

export const ButtonTooltip: FC<ButtonTooltipProps> = ({ title, children }) => {
  const theme = useTheme();
  const fullscreenContainer = document.fullscreenElement as HTMLElement | null;

  return (
    <PuiTooltip
      title={title}
      placement='top'
      arrow
      PopperProps={{
        container: fullscreenContainer,
      }}
      componentsProps={{
        tooltip: {
          sx: {
            '&.MuiTooltip-tooltip': {
              padding: '8px',
              backgroundColor: theme.palette.grey[600],
              boxShadow: '0 8px 16px -2px rgba(27, 33, 44, 0.12)',
              marginBottom: '12px',
            },
            '& .MuiTooltip-arrow': {
              color: theme.palette.grey[600],
            },
          },
        },
      }}
    >
      {children}
    </PuiTooltip>
  );
};
