import React from 'react';

import { PuiCircularProgress } from '../../..';
import { StyledProgressWrapper } from './StyledComponents';
import type { UpdateVideoState } from './types';

interface VideoElementProps {
  src?: string;
  videoRef: React.RefObject<HTMLVideoElement>;
  isFullscreen: boolean;
  toggleFullscreen: () => void;
  isLoading: boolean;
  onTogglePlay: () => void;
  updateVideoState: UpdateVideoState;
  style?: React.CSSProperties;
}

export const VideoElement = ({
  src,
  videoRef,
  isFullscreen,
  toggleFullscreen,
  isLoading,
  onTogglePlay,
  updateVideoState,
  style,
}: VideoElementProps) => (
  <>
    <video
      ref={videoRef}
      src={src}
      onDoubleClick={() => toggleFullscreen()}
      onClick={onTogglePlay}
      onLoadStart={() => updateVideoState('isLoading', true)}
      onWaiting={() => updateVideoState('isLoading', true)}
      onCanPlay={() => updateVideoState('isLoading', false)}
      onPlaying={() => updateVideoState('isLoading', false)}
      onTimeUpdate={e => updateVideoState('currentTime', e.currentTarget.currentTime)}
      onDurationChange={e => updateVideoState('duration', e.currentTarget.duration)}
      onEnded={() => updateVideoState('isPlaying', false)}
      width='100%'
      height='100%'
      style={style}
    >
      <track kind='captions' />
    </video>
    {isLoading && (
      <StyledProgressWrapper>
        <PuiCircularProgress size={isFullscreen ? 128 : 64} />
      </StyledProgressWrapper>
    )}
  </>
);
