import clsx from 'clsx';
import type { CSSProperties, FC } from 'react';
import { useCallback, useEffect, useRef, useState } from 'react';

import { ControlsBottom } from './ControlsBottom';
import { ControlsTop } from './ControlsTop';
import { StyledVideoPlayerWrapper } from './StyledComponents';
import type { UpdateVideoState, VideoPlayerState } from './types';
import { useFullscreen } from './useFullscreen';
import { VideoElement } from './VideoElement';

export interface PuiVideoPlayerProps {
  src?: string;
  fileName: string;
  width?: string | number;
  height?: string | number;
  maxWidth?: string | number;
  maxHeight?: string | number;
  style?: CSSProperties;
}

export const PuiVideoPlayer: FC<PuiVideoPlayerProps> = ({
  src,
  fileName,
  width = 440,
  height = 248,
  maxWidth,
  maxHeight,
  style,
}) => {
  const videoRef = useRef<HTMLVideoElement | null>(null);
  const rootRef = useRef<HTMLDivElement | null>(null);

  const { isFullscreen, toggleFullscreen } = useFullscreen(rootRef);

  const [isHovered, setIsHovered] = useState<boolean>(false);
  const [videoState, setVideoState] = useState<VideoPlayerState>({
    isPlaying: false,
    isMuted: false,
    volume: 1,
    duration: 0,
    currentTime: 0,
    playbackRate: 1,
    isLoading: true,
  });
  const showControls = isHovered || isFullscreen;

  const updateVideoState = useCallback<UpdateVideoState>((key, value) => {
    setVideoState(prev => ({
      ...prev,
      [key]:
        typeof value === 'function'
          ? (value as (prev: VideoPlayerState[typeof key]) => VideoPlayerState[typeof key])(prev[key])
          : value,
    }));
  }, []);

  useEffect(() => {
    const video = videoRef.current;
    if (!video) return;

    updateVideoState('isPlaying', false);
    updateVideoState('currentTime', 0);
    updateVideoState('duration', 0);
    updateVideoState('isLoading', true);
  }, [src, updateVideoState]);

  const handleKeyDown = (e: React.KeyboardEvent<HTMLDivElement>) => {
    const tag = document.activeElement?.tagName ?? '';
    if (['INPUT', 'TEXTAREA', 'SELECT'].includes(tag)) {
      return;
    }

    if (e.code === 'Space') {
      e.preventDefault();
      togglePlay();
    }
    if (e.code === 'ArrowRight') {
      seekBy(5);
    }
    if (e.code === 'ArrowLeft') {
      seekBy(-5);
    }
    if (e.code === 'KeyM') {
      toggleMute();
    }
    if (e.code === 'KeyF') {
      toggleFullscreen();
    }
    if (e.code === 'ArrowUp') {
      changeVolumeBy(0.05);
    }
    if (e.code === 'ArrowDown') {
      changeVolumeBy(-0.05);
    }
  };

  const togglePlay = async () => {
    const video = videoRef.current;
    if (!video) {
      return;
    }

    try {
      if (video.paused) {
        await video.play();
        updateVideoState('isPlaying', true);
      } else {
        video.pause();
        updateVideoState('isPlaying', false);
      }
    } catch (err) {
      console.warn(err);
    }
  };

  const toggleMute = () => {
    updateVideoState('isMuted', prev => {
      const video = videoRef.current;

      if (!video) {
        return prev;
      }
      video.muted = !prev;

      return !prev;
    });
  };

  const changeVolumeBy = (delta: number) => {
    updateVideoState('volume', prev => {
      const video = videoRef.current;

      if (!video) {
        return prev;
      }
      const result = Math.min(1, Math.max(0, +(prev + delta).toFixed(2)));
      video.volume = result;

      return result;
    });
  };

  const seekBy = (seconds: number) => {
    const video = videoRef.current;
    if (!video) {
      return;
    }
    video.currentTime = Math.min(Math.max(0, video.currentTime + seconds), videoState.duration);
    updateVideoState('currentTime', video.currentTime);
  };

  return (
    <StyledVideoPlayerWrapper
      ref={rootRef}
      tabIndex={0}
      onKeyDown={handleKeyDown}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      width={width}
      height={height}
      maxWidth={maxWidth}
      maxHeight={maxHeight}
      className={clsx({ fullscreen: isFullscreen })}
    >
      <VideoElement
        src={src}
        isLoading={videoState.isLoading}
        onTogglePlay={togglePlay}
        videoRef={videoRef}
        updateVideoState={updateVideoState}
        style={style}
        isFullscreen={isFullscreen}
        toggleFullscreen={toggleFullscreen}
      />

      {showControls && (
        <ControlsTop
          fileName={fileName}
          isFullscreen={isFullscreen}
          toggleFullscreen={toggleFullscreen}
        />
      )}

      <ControlsBottom
        isFullscreen={isFullscreen}
        showControls={showControls}
        toggleMute={toggleMute}
        togglePlay={togglePlay}
        updateVideoState={updateVideoState}
        videoRef={videoRef}
        videoState={videoState}
      />
    </StyledVideoPlayerWrapper>
  );
};
