import clsx from 'clsx';

import { PuiIcon, PuiSvgIcon, PuiTypography } from '../../..';
import { ButtonTooltip } from './ButtonTooltip';
import { StyledControlsTop, StyledExpandButton, StyledFileName } from './StyledComponents';

interface ControlsTopProps {
  fileName: string;
  isFullscreen: boolean;
  toggleFullscreen: () => void;
}

export const ControlsTop = ({ fileName, isFullscreen, toggleFullscreen }: ControlsTopProps) => (
  <StyledControlsTop className={clsx({ fullscreen: isFullscreen })}>
    <StyledFileName className={clsx({ fullscreen: isFullscreen })}>
      <PuiTypography
        variant={isFullscreen ? 'body-m-medium' : 'body-sm-semibold'}
        color='white'
      >
        {fileName}
      </PuiTypography>
    </StyledFileName>

    <ButtonTooltip title={isFullscreen ? 'Close' : 'Expand'}>
      <StyledExpandButton
        onClick={toggleFullscreen}
        className={clsx({ fullscreen: isFullscreen })}
      >
        <PuiSvgIcon
          width={20}
          height={20}
          icon={isFullscreen ? PuiIcon.XClose : PuiIcon.Expand1}
          stroke='white'
        />
      </StyledExpandButton>
    </ButtonTooltip>
  </StyledControlsTop>
);
